<template>
<div class="grid grid-cols-4 grid-rows-6 gap-x-6 gap-y-7">
  <div v-for="sit in site" class="card text-center shadow-2xl">
    <figure class="px-3 pt-3">
      <img src="sit.logoimg" class="rounded-xl">
    </figure>
    <div class="card-body">
      <h2 class="card-title">sit.name</h2>
      <p>sit.description</p>
      <div class="justify-center card-actions">
        <button class="btn btn-outline btn-accent" @click="gotodapp(sit.url)">Get Start</button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: "List",
  computed:{
    ...mapState([
        "contract",
        "site"
    ])
  },
  mounted:async function(){
    let count=100
  if(this.site.length==0){

    while(true){
      count+=1
      let site={}
      let result=null
      try{
        result=await this.contract.TokenURI(count)
      }catch{
        console.log("rpc 错误，超出了范围！！！")
        break
      }
      if(result[2]==""){
        break
      }
      site["logoimg"]=result[0]
      site["url"]=result[1]
      site["name"]=result[2]
      site["description"]=result[3]
      console.log(result,typeof result,result[0],result[1],result[2],result[3])
      this.$store.dispatch("addSite",{
        Site:site
      })
    }

  }

  },
  methods:{
    gotodapp(url){
      window.open(url)
    }
  }
}
</script>

